interface s {
  role: string
  email: string
  account: string
  password: string
  confirmPassword: string
}

//TODO:  onSignUpSubmitValid
export function isSignUpValid(props: s, data: s, emailRule: RegExp) {
  // 
  if (!data.role.trim()) {
    props = {...props, role: ''}
  } else {
    props = {...props, role: ''}
  }

  // Email
  if (!data.email.trim()) {
    props = {...props, email: ''}
  } else {
    props = {...props, email: ''}
  }

  // Email
  if (data.email.trim() && !emailRule.test(data.email)) {
    props = {...props, email: 'Email'}
  }
  if (data.email.trim() && emailRule.test(data.email)) {
    props = {...props, email: ''}
  }

  // 
  if (!data.account.trim()) {
    props = {...props, account: ''}
  } else {
    props = {...props, account: ''}
  }

  // 
  if (data.password.trim() !== data.confirmPassword.trim()) {
    props = {
      ...props,
      password: '',
      confirmPassword: '',
    }
  } else {
    props = {...props, password: '', confirmPassword: ''}
  }

  // 
  if (!data.password.trim()) {
    props = {...props, password: ''}
  }

  // 
  if (!data.confirmPassword.trim()) {
    props = {...props, confirmPassword: ''}
  }

  //
  if (data.account.includes(' ')) {
    props = {...props, account: ''}
  }

  //
  if (data.password.includes(' ')) {
    props = {...props, password: ''}
  }

  //
  if (data.confirmPassword.includes(' ')) {
    props = {...props, confirmPassword: ''}
  }

  return props
}

//TODO:  onSingUpInputChangeValid
export function signUpValueValid(props: s, inputName: string, value: string, password: string, confirmPassword: string) {
  if (!value.trim()) {
    props = {
      ...props, [inputName]: ''
    }
  }
  else {
    props = {...props, [inputName]: ''}
  }


  if (inputName === 'password' && value && value !== confirmPassword) {
    props = {
      ...props, ['password']: '', ['confirmPassword']: ''
    }
  }

  if (inputName === 'password' && value && value === confirmPassword) {
    props = {
      ...props, ['password']: '', ['confirmPassword']: ''
    }
  }


  if (inputName === 'confirmPassword' && value && value !== password) {
    props = {
      ...props, ['password']: '', ['confirmPassword']: ''
    }
  }


  if (inputName === 'confirmPassword' && value && value === password) {
    props = {
      ...props, ['password']: '', ['confirmPassword']: ''
    }
  }

  return props
}

export function isEmailValue(props: s, value: string) {
  if (!value.trim()) {
    props = {...props, email: ''}
  } else {
    props = {...props, email: ''}
  }
  return props
}

export function isRoleValue(props: s, value: string) {
  if (!value.trim()) {
    props = {...props, role: ''}
  } else {
    props = {...props, role: ''}
  }
  return props
}

export function isAccountValue(props: s, value: string) {
  if (!value.trim()) {
    props = {...props, account: ''}
  } else {
    props = {...props, account: ''}
  }
  return props
}

export function isPasswordValue(
  props: s,
  value: string,
  confirmPassword: string
) {
  if (value && value !== confirmPassword) {
    props = {
      ...props,
      password: '',
      confirmPassword: '',
    }
  } else {
    props = {...props, password: '', confirmPassword: ''}
  }
  if (!value.trim() && !confirmPassword) {
    props = {...props, password: '', confirmPassword: ''}
  }
  if (!value.trim()) {
    props = {...props, password: ''}
  }
  if (value && value === confirmPassword) {
    props = {...props, password: '', confirmPassword: ''}
  }
  return props
}

export function isConfirmPasswordValue(
  props: s,
  value: string,
  password: string
) {
  if (value && value !== password) {
    props = {
      ...props,
      password: '',
      confirmPassword: '',
    }
  }
  if (!value.trim() && !password) {
    props = {...props, password: '', confirmPassword: ''}
  }
  if (!value.trim()) {
    props = {...props, confirmPassword: ''}
  }
  if (value && value === password) {
    props = {...props, password: '', confirmPassword: ''}
  }
  return props
}